body {
  font-family: "Inter";
  background-color: #e7f6ff;
}

* {
  font-family: "Inter";
}

.wrapper {
  background: #fff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 0px auto;
}

header {
  border-bottom: 1px solid #eeeeee;

  img {
    margin-right: 15px;
  }

  h3,
  p {
    margin: 0;
  }
}

.cardContainer {
  flex-wrap: wrap;
}

.content {
  h1 {
    margin: 0;
  }
}

.searchBlock {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }
}

.profile-icons {
  img {
    cursor: pointer;
  }
}
