.card {
    position: relative;
    margin-right: 30px;
    margin-bottom: 30px;
    border: 1px solid #f3f3f3;
    padding: 30px;
    width: 220px;
    border-radius: 40px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

    h5 {
        font-weight: 400;
        font-size: 14px;
    }

    &:hover {
        box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.07);
        transform: translateY(-5px);
    }


    .favorite {
        position: absolute;
        cursor: pointer;
        transform: translate(-12px, -12px);

        img {
            border: 1px solid rgba(0, 0, 0, 0.4);
            border-radius: 50%;
        }
    }

    span {
        opacity: 0.5;
        font-size: 13px;
        text-transform: uppercase;
    }

    b {
        font-size: 14px;
    }
}

.button {
    background: #fff;
    border-radius: 8px;
    box-sizing: border-box;
    border: 1px solid #f2f2f2;
    height: 32px;
    width: 32px;
    cursor: pointer;
}

.btnChecked {
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 8px;
}